import React from 'react';

import {lazy} from 'react';

// project import
import Loadable from 'components/Loadable';
import MainLayout from 'layout/MainLayout';
import NeighborIcareDetail from 'pages/icare/NeighborIcareDetail';
const DefaultPage = Loadable(lazy(() => import('pages/DefaultPage')));
const MyIcare = Loadable(lazy(() => import('pages/icare/MyIcare')));
const CreateMyIcare = Loadable(lazy(() => import('pages/icare/CreateMyIcare')));
const MyIcareDetail = Loadable(lazy(() => import('pages/icare/MyIcareDetail')));
const NeighborIcare = Loadable(lazy(() => import('pages/icare/NeighborIcare')));
const CreateNeighborIcare = Loadable(lazy(() => import('pages/icare/CreateNeighborIcare')));
const Matching = Loadable(lazy(() => import('pages/icare/Matching')));
const MatchingDetail = Loadable(lazy(() => import('pages/icare/MatchingDetail')));
const Review = Loadable(lazy(() => import('pages/member/Review')));
const Mileage = Loadable(lazy(() => import('pages/member/Mileage')));
const Detail = Loadable(lazy(() => import('pages/member/Detail')));

// [1 게시판] 게시판(Board) 페이지 컴포넌트 등록
const Board = Loadable(lazy(() => import('pages/board/Board')));
// [2 게시글 작성] Todo: 게시글 작성(CreatePost) 페이지 컴포넌트 등록
const CreatePost = Loadable(lazy(() => import('pages/board/CreatePost')));
// [3 게시글 상세] Todo: 게시글 상세(Post) 페이지 컴포넌트 등록
const Post = Loadable(lazy(() => import('pages/board/Post')));
// [4 게시판 수정] Todo: 게시판 수정(UpdatePost) 페이지 컴포넌트 등록
const UpdatePost = Loadable(lazy(() => import('pages/board/UpdatePost')));

// ==============================|| MAIN ROUTING ||============================== //

const MainRoutes = {
	path: '/',
	element: <MainLayout />,
	children: [
		{
			path: '',
			element: <DefaultPage />,
		},
		{
			path: 'my-icare',
			element: <MyIcare />,
		},
		{
			path: 'my-icare/write',
			element: <CreateMyIcare />,
		},
		{
			path: 'my-icare/:id',
			element: <MyIcareDetail />,
		},
		{
			path: 'neighbor-icare',
			element: <NeighborIcare />,
		},
		{
			path: 'neighbor-icare/write',
			element: <CreateNeighborIcare />,
		},
		{
			path: 'neighbor-icare/:id',
			element: <NeighborIcareDetail />,
		},
		{
			path: 'matching',
			element: <Matching />,
		},
		{
			path: 'matching/:id',
			element: <MatchingDetail />,
		},
		{
			path: 'member/review',
			element: <Review />,
		},
		{
			path: 'member/mileage',
			element: <Mileage />,
		},
		{
			path: 'member/detail',
			element: <Detail />,
		},
		// [1 게시판] 게시판(Board) 페이지 라우팅
		{
			path: 'board',
			element: <Board />,
		},

		// [2 게시글 작성] Todo: 게시글 작성(CreatePost) 페이지 라우팅('post/write')
		{
			path: 'post/write',
			element: <CreatePost />,
		},

		// [3 게시글 상세] 게시글 상세(Post) 페이지 라우팅
		{
			path: 'post/:id',
			element: <Post />,
		},
		// [4 게시글 수정] Todo: 게시글 수정(UpdatePost) 페이지 라우팅('post/update')
		{
			path: 'post/update',
			element: <UpdatePost />,
		},
	],
};

export default MainRoutes;
