import React from 'react';
import PropTypes from 'prop-types';
import {useEffect, useRef, useState} from 'react';

// material-ui
import {useTheme} from '@mui/material/styles';
import {Box, ButtonBase, CardContent, ClickAwayListener, Grid, IconButton, Paper, Popper, Stack, Typography, Avatar} from '@mui/material';

// project import
import MainCard from 'components/MainCard';
import Transitions from 'components/@extended/Transitions';

// assets
import {LogoutOutlined} from '@ant-design/icons';
import ProfileTab from './ProfileTab';
import {useNavigate} from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {logout} from 'store/reducers/user';

// tab panel wrapper
function TabPanel({children, value, index, ...other}) {
	return (
		<div role='tabpanel' hidden={value !== index} id={`profile-tabpanel-${index}`} aria-labelledby={`profile-tab-${index}`} {...other}>
			{value === index && children}
		</div>
	);
}

TabPanel.propTypes = {
	children: PropTypes.node,
	index: PropTypes.any.isRequired,
	value: PropTypes.any.isRequired,
};

// function a11yProps(index) {
// 	return {
// 		id: `profile-tab-${index}`,
// 		'aria-controls': `profile-tabpanel-${index}`
// 	};
// }

// ==============================|| HEADER CONTENT - PROFILE ||============================== //

const Profile = () => {
	const theme = useTheme();

	const navigate = useNavigate();
	const dispatch = useDispatch();

	//const user = useSelector((state) => state.user);

	const user = {eamil: 'muryongjeon@sk.com', name: '전무룡', mileage: '5,000'};
	const {email, name, mileage} = user;

	const handleLogout = async () => {
		dispatch(logout());

		navigate('/auth/login');
	};

	const anchorRef = useRef(null);
	const [open, setOpen] = useState(false);

	useEffect(() => {}, []);

	const handleToggle = () => {
		setOpen((prevOpen) => !prevOpen);
	};

	const handleClose = (event) => {
		if (anchorRef.current && anchorRef.current.contains(event.target)) {
			return;
		}
		setOpen(false);
	};

	// eslint-disable-next-line no-unused-vars
	const [value, setValue] = useState(0);

	// const handleChange = (event, newValue) => {
	// 	setValue(newValue);
	// };

	const iconBackColorOpen = 'grey.300';

	return (
		<Box sx={{flexShrink: 0, ml: 0.75}}>
			<ButtonBase
				sx={{
					p: 0.25,
					bgcolor: open ? iconBackColorOpen : 'transparent',
					borderRadius: 1,
					'&:hover': {bgcolor: 'secondary.lighter'},
				}}
				aria-label='open profile'
				ref={anchorRef}
				aria-controls={open ? 'profile-grow' : undefined}
				aria-haspopup='true'
				onClick={handleToggle}>
				<Stack direction='row' spacing={2} alignItems='center' sx={{p: 0.5}}>
					{user.name != '' ? (
						<Grid item>
							<Avatar alt='Natacha' src='/avatar2.jpeg' />
						</Grid>
					) : null}

					<Grid item>
						<Typography variant='subtitle1'>{name}</Typography>
					</Grid>
				</Stack>
			</ButtonBase>
			<Popper
				placement='bottom-end'
				open={open}
				anchorEl={anchorRef.current}
				role={undefined}
				transition
				disablePortal
				popperOptions={{
					modifiers: [
						{
							name: 'offset',
							options: {
								offset: [0, 9],
							},
						},
					],
				}}>
				{({TransitionProps}) => (
					<Transitions type='fade' in={open} {...TransitionProps}>
						{open && (
							<Paper
								sx={{
									boxShadow: theme.customShadows.z1,
									width: 290,
									minWidth: 240,
									maxWidth: 290,
									[theme.breakpoints.down('md')]: {
										maxWidth: 250,
									},
								}}>
								<ClickAwayListener onClickAway={handleClose}>
									<MainCard elevation={0} border={false} content={false}>
										<CardContent sx={{px: 2.5, pt: 3}}>
											<Grid container justifyContent='space-between' alignItems='center'>
												<Grid item>
													<Stack direction='row' spacing={1.25} alignItems='center'>
														<Stack>
															<Typography variant='h6'>{name}</Typography>
															<Typography variant='body2' color='textSecondary'>
																{email}
															</Typography>
															<Typography mt={2} variant='body2'>
																보유 마일리지 : {mileage}
															</Typography>
														</Stack>
													</Stack>
												</Grid>
												<Grid item>
													<IconButton size='large' color='secondary' onClick={handleLogout}>
														<LogoutOutlined />
													</IconButton>
												</Grid>
											</Grid>
										</CardContent>
										{open && (
											<>
												<Box
													sx={{
														borderBottom: 1,
														borderColor: 'divider',
													}}
												/>
												<TabPanel value={value} index={0} dir={theme.direction}>
													<ProfileTab handleLogout={handleLogout} />
												</TabPanel>
											</>
										)}
									</MainCard>
								</ClickAwayListener>
							</Paper>
						)}
					</Transitions>
				)}
			</Popper>
		</Box>
	);
};

export default Profile;
