import React from 'react';
import {Dialog, DialogTitle, DialogContent, Avatar, Grid, Typography, Rating, Divider} from '@mui/material';
import StarIcon from '@mui/icons-material/Star';

function SimpleDialog(props) {
	const {onClose, selectedValue, open} = props;

	const handleClose = () => {
		onClose(selectedValue);
	};

	const value = 4;

	return (
		<Dialog onClose={handleClose} open={open}>
			{/* <Grid container direction='row' alignItems='center'>
				<Grid item>
					<Avatar alt='Natacha' src='/avatar4.jpeg' />
				</Grid>
				<Grid item>
					<Typography variant='h3'>{'전무룡'}님의 정보</Typography>
				</Grid>
			</Grid> */}
			<DialogTitle>
				<Grid container direction='row' alignItems='center' spacing={2}>
					<Grid item>
						<Avatar alt='Natacha' src='/avatar4.jpeg' />
					</Grid>
					<Grid item>
						<Typography variant='h4'>{'전무룡'}님의 상세정보</Typography>
					</Grid>
				</Grid>
			</DialogTitle>
			<Divider style={{margin: '0 15px 0 15px'}} />
			<DialogContent>
				<Grid container direction='column' spacing={3}>
					<Grid item>
						<Typography variant='h5'>메일주소</Typography>
						<Typography variant='body1'>amf@sk.com</Typography>
					</Grid>
					<Grid item>
						<Typography variant='h5'>연락처</Typography>
						<Typography variant='body1'>010-7460-3309</Typography>
					</Grid>
					<Grid item>
						<Typography variant='h5'>나이</Typography>
						<Typography variant='body1'>34</Typography>
					</Grid>
					<Grid item>
						<Typography variant='h5'>자기소개</Typography>
						<Typography variant='body1'>안녕하세요!</Typography>
					</Grid>
					<Grid item>
						<Typography variant='h5'>평점</Typography>
						<Rating name='text-feedback' value={value} readOnly precision={0.5} emptyIcon={<StarIcon style={{opacity: 0.55}} fontSize='inherit' />} />
					</Grid>
				</Grid>
			</DialogContent>
		</Dialog>
	);
}

export default SimpleDialog;
